import axios from "axios";
import { useEffect, useState } from "react";
import { Col, Container, Nav, Row, Navbar } from "react-bootstrap";
import { FaFacebook, FaTelegram, FaTwitterSquare, FaGooglePlus } from 'react-icons/fa';
import { NavLink } from "react-router-dom";
import logo from "../../../assets/images/logo.png";
import config from "../../../config/siteConfig";
import "./navbar.scss";

export default function NavbarMenu() {

    const [data, setData] = useState();
    const [acil, setAcil] = useState();

    useEffect(() => {
        var g;
        const getData = () => {
            axios.get(config.apiUrl + "/GetKategoriler").then(res => {
                g = res.data.acilHaberler;
                setData(res.data);
            })
        }
        var count = 0;
        const changeHaber = () => {
            setAcil(g[count]);
            count = count + 1

            if (count > 5) count = 0;
        }

        setInterval(changeHaber, 5000);

        getData()
    }, []);

    return (
        <div>
            <header>
                <div className="header-area">
                    <div className="main-header ">
                        <div className="header-top black-bg  d-md-block">
                            <Container>
                                <Row>
                                    <Col xs={12} sm={6}>
                                        <div className="header-info-right">
                                            <ul className="header-social  m-b-0">
                                                <li><a href={data?.urls?.google}>  <FaGooglePlus /></a></li>
                                                <li><a href={data?.urls?.facebook}> <FaFacebook /></a></li>
                                                <li><a href={data?.urls?.telegram}><FaTelegram /></a></li>
                                                <li><a href={data?.urls?.twiiter}><FaTwitterSquare /></a></li>
                                            </ul>
                                        </div>
                                    </Col>
                                    <Col xs={12} sm={6}>
                                        <div className="header-info-left text-left">
                                            <ul className="m-b-0">
                                                <li style={{ marginRight: 0 }}><img src="assets/img/icon/header_icon1.png" alt="" />34ºc, Sunny </li>
                                                <li><img src="assets/img/icon/header_icon1.png" alt="" />Tuesday, 18th June, 2019
                                                </li>
                                            </ul>
                                        </div>
                                    </Col>
                                </Row>
                            </Container>
                        </div>
                        <div className="header-mid  d-md-block p-0">
                            <Row>
                                <Col sm={9}>
                                    <div className="text-animation">
                                        <div>
                                            <h2>{acil}</h2>
                                        </div>
                                    </div>
                                </Col>
                                <Col sm={3}>
                                    <div className="logo p-t-10 p-m-10 text-center">
                                        <a href="index.html"><img src={logo} style={{ width: "150px", height: "115px" }} alt="" /></a>
                                    </div>
                                </Col>

                                <Col lg={12}>
                                    <Navbar bg="light" expand="lg">
                                        <Container>
                                            <Navbar.Toggle aria-controls="basic-navbar-nav" />
                                            <Navbar.Collapse id="basic-navbar-nav">
                                                <Nav>
                                                    <NavLink className="nav-link" to={""} active="true" >الصفحة الرئيسية</NavLink>
                                                    {
                                                        data?.list.map((v, i) => (
                                                            <NavLink className="nav-link" to={"kategori/"+v.id}>{v.name}</NavLink>
                                                        ))
                                                    }
                                                </Nav>
                                            </Navbar.Collapse>
                                        </Container>
                                    </Navbar>
                                </Col>
                            </Row>
                        </div>

                    </div>
                </div>
            </header>
        </div>
    )
}
